@import url(https://fonts.googleapis.com/css2?familyfamily-Lato:wght@300;400;700&display-swap);
::webkit-scrollbar{
	width: 5px;
	height: 6px
}

::webkit-scrollbar-track{
	box-shadow: inset 0 0 5px #a5aaad;
	border-radius: 10px
}

::webkit-scrollbar-thumb{
	background: #3ea175;
	border-radius: 10px;
}

::webkit-scrollbar-thumb:hover{
	background: #a5aaad;
}

* {
	margin: 0;
	padding: 0;
}

body{
	box-sizing: border-box;
	font-family: 'Lato', sans-serif;

}

.text-primary-p{
	color: #a5aaad;
	font-size: 14px;
	font-weight: 700;
}

.font-bold{
	font-weight: 700;
}

.text-title{
	color: #2e4a66;
}

.text-lightblue{
	color: #469cac;
}

.text-red{
	color: #cc3d38;
}

.text-yellow{
	color: #a98921;
}

.text-green{
	color: #3b9668;
}

.container{
	display: grid;
	height: 100vh;
	grid-template-columns: 0.8fr repeat(3, 1fr);
	grid-template-rows: 0.2fr 3fr;
	grid-template-areas:
	'sidebar nav nav nav'
	'sidebar main main main';
}

@media only screen and (max-width: 978px){
	.container{
		grid-template-columns: 1fr;
		grid-template-rows: 0.2fr 3fr;
		grid-template-areas:"nav" "main";
	}
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.navbar {
    background: rgb(255, 255, 255);
    grid-area: nav;
    height: 60px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 30px 0 30px;
    border-bottom: 1px solid lightgray;
}

.nav_icon {
    display: none;
}

.nav_icon > i {
    font-size: 26px;
    color: #a5aaad;
}

.navbar__left > a {
    margin-right: 30px;
    text-decoration: none;
    color: #a5aaad;
    font-size: 15px;
    font-weight: 700;
}

.navbar__left .active_link {
    color: #265acc;
    border-bottom: 3px solid #265acc;
    padding-bottom: 12px;
}

.navbar__right {
    display: flex;
    justify-content: center;
    align-items: center;
}

.navbar__right > a {
    margin-left: 20px;
    text-decoration: none;
}

.navbar__right > a > i {
    color: #a5aaad;
    font-size: 16px;
    border-radius: 50px;
    background: #FFF;
    box-shadow: 2px 2px 5px #d9d9d9, -2px -2px 5px #FFFF;
    padding: 7px;
}

@media only screen and (max-width: 978px){
    .nav_icon {
        display: inline;
    }
}

@media only screen and (max-width: 400px) {
    .navbar__left {
        display: none;
    }
}
main{
    background-color: #f3f4f6;
    grid-area: main;
    overflow-y: auto;
}

.main__container{
    padding: 20px 35px;
}

.main__title{
    display: flex;
    align-items: center;
}

.main__title > img{
    max-height: 100px;
    object-fit: contain;
    margin-right: 20px;
}

.main__greeting > h1 {
    font-size: 24px;
    color: #2e4a66;
    margin-bottom: 5px;
}

.main__greeting > p {
    font-size: 14px;
    font-weight: 700;
    color: #a5aaad;
}

.main__cards{
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 30px;
    gap: 30px;
    margin: 20px 0;
}

.card{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    height: 70px;
    padding: 25px;
    border-radius: 5px;
    background-color: #FFF;

    box-shadow:  5px 5px 13px #ededed, -5px -5px 13px #FFF;
}

.card_inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.card_inner > span{
    font-size: 25px;
}

.charts{
    display: grid;
    grid-template-columns: repeat(2, 1rf);
    grid-gap: 30px;
    gap: 30px;
    margin-top: 50px;
}

.charts__left{
    padding: 25px;
    border-radius: 5px;
    background: #FFF;
    box-shadow: 5px 5px 13px #ededed, -5px -5px 13px #FFF;
}

.charts__left_title{
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.charts__left_title > div > h1{
    font-size: 24px;
    color: #2e4a66;
    margin-bottom: 5px;
}

.charts__left_title > div > p{
    font-size: 14px;
    font-weight: 700;
    color: #a5aaad;
}

.charts__left_title > i{
    color: #FFF;
    font-size: 20px;
    background: #FFC100;
    border-radius: 200px 0px 200px 200px;
    -moz-border-radius: 200px 0px 200px 200px;
    -webkit-border-radius: 200px 0px 200px 200px;
    border: 0px solid #000;
    padding: 15px;
}

.charts__right{
    padding: 25px;
    border-radius: 5px;
    background: #FFF;
    box-shadow: 5px 5px 13px #ededed, -5px -5px 13px #FFF;
}

.charts__right__title{
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.charts__right_title > div > h1{
    font-size: 24px;
    color: #2e4a66;
    margin-bottom: 5px;
}

.charts__right_title > div > p{
    font-size: 14px;
    font-weight: 700;
    color: #a5aaad;
}

.charts__right_title > i{
    color: #FFF;
    font-size: 20px;
    background: #39447a;
    border-radius: 200px 0px 200px 200px;
    border: 0px solid #000;
    padding: 15px;
}

.charts__right__cards{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 20px;
    gap: 20px;
    margin-top: 30px;
}

.card1{
    background: #d1ecf1;
    color: #35a4ba;
    text-align: center;
    padding: 25px;
    border-radius: 5px;
    font-size: 14px;
}

.card2{
    background: #d2f9ee;
    color: #38e1b0;
    text-align: center;
    padding: 25px;
    border-radius: 5px;
    font-size: 14px;
}

@media only screen and (max-width: 855px){
    .main__cards{
        grid-template-columns: 1fr;
        grid-gap: 10px;
        gap: 10px;
        margin-bottom: 0;
    }    

    .charts{
        grid-template-columns: 1fr;
        margin-top: 30px;
        justify-content: center;
    }

    .charts__right__cards{
        grid-template-columns: 1fr;
        align-items: center;
        margin-top: 30px;
    }
}
#sidebar {
 background: #000000;
 grid-area: sidebar;
 padding: 20px;
 overflow-y: auto;
 transition: all 0.5s;
}

.sidebar__title {
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: #ffffff;
    margin-bottom: 30px;
}

.sidebar__img{
    display: flex;
    align-items: center;
}

.sidebar__title > div > img {
    width: 55px;
    object-fit: contain;
    border-radius: 50%;
}

.sidebar__title > div > h1 {
    font-size: 18px;
    display: inline;
    margin-left: 15px;
}

.sidebar__title > div > i {
    font-size: 18px;
    display: none;
}

.sidebar__menu > h2 {
    color: #ffffff;
    font-size: 16px;
    margin-top: 15px;
    margin-bottom: 5px;
    padding: 0 10px;
    font-weight: 700;
}

.sidebar__link {
    color: #ffffff;
    padding: 10px;
    border-radius: 3px;
    margin-bottom: 5px;
}

.active_menu_link {
    background: #6B3685;
    color: #ffffff;
}

.active_menu_link a {
    color: #ffffff !important;
}

.sidebar__link >a {
    text-decoration: none;
    color: #ffffff;
    font-weight: 700;
}

.sidebar__link a:focus {
    padding: 7px;
    background: #6B3685;
    border-radius: 3px;
}

.sidebar__link > i {
    margin-right: 10px;
    font-size: 18px;
}

.sidebar__logout {
    margin-top: 200px;
    padding: 10px;
    color: #e65061;
}

.sidebar__logout > a {
    text-decoration: none;
    color: #e65061;
    font-weight: 700;
    text-transform: uppercase;
}

.sidebar__logout > i {
    margin-right: 10px;
    font-size: 18px;
}

.sidebar__responsive {
    display: inline !important;
    z-index: 9999 !important;
    left: 0 !important;
    position: absolute;
}
@media only screen and (max-width: 978px) {
    #sidebar {
        display: none;
    }
    .sidebar__title > i {
        display: inline;
    }
}
